@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.topContainer{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width:100vw;
    padding-top: 2vh;
}
.header{
    display:flex;
    flex-direction:row;
    justify-content: space-around;
    align-items: center;
    flex-wrap:wrap;
}
.header > div {
    margin : 0vw 2vw 0vw 2vw;
}
.navBar{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    border-bottom: 0.1vw black solid;
    padding: 2vh;
}
.sticky{
    position:fixed;
    top:0;
    width:100%;
    z-index: 2;
    background-color: hsl(0, 0%, 98%);
}
.navBar > div{
    min-width: 200px;
}
.is-clickable{
    cursor: pointer;
}
.hwbsvgDiv{
    height:50%;
    width:50%;
    min-width: 400px;
}
.socialDivs{
    display:flex;
    justify-content: center;
    align-items: center;
}
a{
    height:-webkit-min-content;
    height:-moz-min-content;
    height:min-content;
    width:-webkit-min-content;
    width:-moz-min-content;
    width:min-content;
}
p{
    font-family: 'Poppins', sans-serif;
    text-align: center;
    vertical-align: center;
    font-size: 1.5em;
}

.socialLogos{
    height:12vh;
    width:12vh;
    min-height:80px;
    min-width:80px;
    transition: opacity 0.5s;
    opacity:0.8;
    color:#455A64;
}
.socialLogos:hover{
    opacity: 1;
}
.links{
    color:#455A64;
}
.links:hover{
    font-weight: 600;
    text-shadow: 0.2vw 0.2vw rgb(201, 201, 201) ;
    transition: text-shadow 0.5s;
    transition-timing-function: ease-out;
}

.mainCard{
    height: 100%;
    align-self: flex-start;
    /* width: 80%; */
    /* background-color: rgb(67, 124, 144); */
    /* border: 2px black;
    border-style: solid; */
    align-items: center; 
    justify-self: center;
    padding: 0.5em;
    display: flex;
    flex-direction: column;
    padding-top: 0px;
}
.contentCard{
    width:80%;
}
.background{
    height:100%;
    width:100%;
    display:flex;
    flex-direction: row;
    justify-content:center;
}

.is-clickable{
    cursor: pointer;
}

.gridContainer{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
    /* grid-template-columns: repeat(2,1fr); */
    /* grid-template-rows:; */
    grid-auto-rows: -webkit-min-content;
    grid-auto-rows: min-content;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    grid-auto-flow: dense;
    text-align: left;
    margin-top: 3vh;
    padding: 1vh 1vw 1vh 1vw;
}
.ProjectCard{
    background-color: rgb(245, 245, 245);
    box-shadow: 5px 5px 10px rgb(200, 200, 200);
    border-radius:3%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding:1vh 1vw 1vh 1vw;
}
.ProjectCard:hover{
    background-color:  rgb(233, 233, 233);
    cursor: pointer;
    box-shadow: 10px 10px 10px rgb(180, 180, 180);
    transition: background-color 0.5s;
    transition: box-shadow 0.7s;
}
.wideProject{
    grid-column: 1/-1;
}
.tallProject{
    grid-row: span 2;
}
.projectTitle{
    font-weight: bold;
    font-size: 2em;
    text-align: inherit;
}
.projectDescription{
    text-align: inherit;
}
h1{
    margin-top: 3vw;
}
.fa-envelope{
    color:rgb(54, 54, 54);
    font-size: 5vw;

}

p{
    vertical-align: center;
    text-align: center;
}
